import React from 'react';
import Scrollspy from 'react-scrollspy';

import Layout from '../components/Layout';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Scroll from '../components/Scroll';
import overLayPic from '../assets/images/overlay.png';
import { Link } from 'gatsby';
import Contact from '../components/Form'

const sections = [
  { id: 'one', bg: require('../assets/images/pic01.jpg') },
  { id: 'two', bg: require('../assets/images/pic02.jpg') },
  { id: 'three', bg: require('../assets/images/pic03.jpg') },
];
const IndexPage = () => (
  <Layout>
    <Header />

    <section id="one" className="main special">
      <div className="container">
        <span className="image fit primary">
          <img src={sections[0].bg} alt="" />
        </span>
        <div className="content">
          <img src={require("../assets/images/sp.jpg")} style={{width:"128px", borderRadius:"50%"}}/>
          <header className="major">
            <h2>Samantha Piro
            <p style={{position:"reletive", top:-20, fontSize:"0.75em"}}>MA, LLPC, RYT200</p>
              
            </h2>
          </header>
          <p>
            As a small child I believed myself to be “too sensitive” or “too emotional”. Everything and
            anything could easily upset me, whether it was from listening to music, hearing people argue,
            or being playfully teased. I began to adapt the belief that my emotions were unhelpful and too scary
            to face so I attempted to “shut down” this emotional aspect of the Self and in doing so went
            down a road of hurt, pain, and loneliness. In my attempt to “turn off” the emotional Self,
            I discovered that all I was doing was abandoning myself, and
            soon I began a journey of self-healing, self-discovery, and self-cultivating.
          </p>
          <p>
            I found healing in feeling - in accepting the feelings about who I am, the feelings about where I am,
            and the feelings I have about my feelings. The key to the freedom from the pain has been the acceptance
            of the Self rather than the denial of. 
          </p>
          <p>
            I believe we all deserve a space to feel safe, a space to exist, a space to be, a space to
            explore any and all aspects of ourselves. Sometimes the healing journey (a life-long journey) can be
            challenging to endure alone, and I do not believe this life is meant to live alone. We all encounter
            suffering, pain, and loss. As a fellow traveler on this healing journey, I would be honored to be a person
            who holds this space for you as you unravel the many complexities of what it is like to be you.
          </p>
        </div>
        <Scroll type="id" element="two">
          <a href="#two" className="goto-next ">
            Next
          </a>
        </Scroll>
      </div>
    </section>

    <section id="two" className="main special">
      <div className="container">
        <span className="image fit primary">
          <img src={sections[1].bg} alt="" />
        </span>
        <div className="content">
          <header className="major">
            <h2>What I do</h2>
          </header>
          <p>
            I work with individuals and couples to explore thoughts and feelings around a variety of issues including
            anxiety, depression, trauma, relationship issues, women's issues, and spirituality.
          </p>
          <p>
            In addition to traditional therapy
            I offer one-on-one yoga and meditation sessions. Yoga has played a significant role in my personal healing process
            as it has provided me the tools for turning inward.
          </p>
          <ul className="icons-grid">
            <li>
              <span className="icon major fas fa-user" />
              <h3>Counseling</h3>
            </li>
            <li>
              <span className="icon major fas fa-user-friends" />
              <h3>Couples Counseling</h3>
            </li>
            <li>
              <img style={{width:"4em"}} src={require("../assets/yoga.svg")} />
              <h3>Yoga</h3>
            </li>
            <li>
              <img src={require("../assets/untitled.svg")} />
              <h3>Meditation</h3>
            </li>
          </ul>
        </div>
        <Scroll type="id" element="three">
          <a href="#three" className="goto-next ">
            Next
          </a>
        </Scroll>
      </div>
    </section>

    <section id="three" className="main special">
      <div className="container">
        <span className="image fit primary">
          <img src={sections[2].bg} alt="" />
        </span>
        <div className="content">
          <header className="major">
            <h2>About Sacred Garden</h2>
          </header>
          <p>
          One day during a meditation, I saw myself walking through an open, white space.
          I came to a door, and with no prompting, I instinctively knew this was a doorway to my heart.
          Not the physical, blood-pumping sense of the word heart, but the access to the love
          that exists within the physical body. I opened the door to discover a vast, limitless garden -
          surrounded by luscious greenery and an abundance of flowers. I was instantly reminded that
          I am sacred. I was reminded how to love from the inside out, and to always be cultivating my sense of self.
          </p>
        </div>
        <Scroll type="id" element="footer">
          <a href="#footer" className="goto-next ">
            Next
          </a>
        </Scroll>
      </div>
    </section>

    <section id="footer">
      <div className="container">
        <header className="major">
          <h2>Get in touch</h2>
        </header>
        <Contact formSpree="https://formspree.io/xoqlowlr" />
      </div>
      <Footer />
    </section>

    <Scrollspy
      items={sections.map(s => s.id)}
      currentClassName="active"
      offset={50}
      componentTag={'div'}
    >
      {sections.map(s => {
        return (
          <div
            key={s.id}
            className="main-bg"
            id={`${s.id}-bg`}
            style={{ backgroundImage: `url(${overLayPic}), url(${s.bg})` }}
          />
        );
      })}
    </Scrollspy>
  </Layout>
);

export default IndexPage;
